html {
  font-family: Roboto, sans-serif;
}

body {
  --font-size: 12px;
  --link-color: #00acdc;
  margin: 0;
}

.header {
  box-sizing: border-box;
  outline: none;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav {
  line-height: 60px;
  display: flex;
}

.nav__list {
  padding-inline-start: 0;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  list-style-type: none;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #000;
  font-size: var(--font-size);
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.is-active {
  text-align: center;
  color: var(--link-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

a[data-qa="hover"] {
  white-space: nowrap;
  overflow: hidden;
}

a[data-qa="hover"]:hover, .is-active:hover {
  color: var(--link-color);
}
